@import "../base/settings";

.add_vouch_con {
    background-color: white;
    width: 90%;
    margin-left: 30px;
    box-shadow: 0px 0px 10px rgb(163, 163, 163);
    height: 200vh;
}
.tbtn {
    cursor: pointer;
    span {
        color: blue;
        text-decoration: underline;
    }
}
.tbtn:hover {
    span {
        color: darkblue;
        text-decoration: underline;
    }
}

.vouch_body {
    display: flex;
    flex-direction: row;
}

.add_vouch_right_btns {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    width: 30%;

    p {
        cursor: pointer;
        color: $grey_text;
    }
}

.vouch_body_right {
    border-left: 2px solid $l_grey_text;
    width: 40%;
}

.vouch_details {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.vouch_si {
    margin: 5px 15px;

    span {
        color: $grey_text;
    }

    input {
        color: $l_grey_text;
        border: 1px solid $grey_text;
        padding: 1px 6px;
        font-size: 1rem;
        width: auto;
        text-align: center;
    }
    select {
        color: $l_grey_text;
        font-size: 1rem;
        border: 1px solid $grey_text;
        width: auto;
        padding: 1px 6px;
        text-align: center;
    }
}

#vouch_bill_no {
    width: 110px;
}
#vouch_gr_no {
    width: 110px;
}
#vouch_transport_name {
    width: 200px;
}
#vouch_sup {
    width: 200px;
}
#vouch_sup_agent {
    width: 200px;
}
#vouch_comission {
    width: 120px;
}

.vouch_customer {
    display: flex;
    flex-direction: row;
}

.vouch_body_middle {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 2px solid $l_grey_text;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    input {
        width: 90px;
    }
}

#vouch_add_btn {
    margin: 10px 20px;
    padding: 5px 30px;
    font-size: $p_font;
    color: $grey_text;
    border: 1px solid $grey_text;
    border-radius: 30px;
    background-color: white;
    cursor: pointer;
}

.vouch_table_con {
    margin-top: 15px;
}

#vouch_table {
    width: 100%;
}

table,
th,
td {
    border: 1px solid $l_grey_text;
    border-collapse: collapse;
    padding: 5px;
    font-size: $p_font;
}

th {
    color: $grey_text;
}

th:nth-child(1) {
    width: 70px;
}
th:nth-child(4) {
    width: 70px;
}
th:nth-child(5) {
    width: 100px;
}

tr:nth-child(even) {
    background-color: #f0f0f0;
}
